.fontcard{
  width: calc(50% - 20px);
  display: inline-block;
  margin: 10px;
  border: 1px solid $gray-border;

  .thumbnail img {
      width: 100%;
  }
}

.actionbar {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 20px;
  }

.family-heading {
  font-size: 18px;
  font-family: $font-regular;
  color: $black;
  margin: 0 0 0px 0;
  line-height: 20px;
}

.foundry-tag {
  font-size: 14px;
  font-family: $font-regular;
  color: $gray-text;
  margin: 0 0 0px 0;
}

.prices {
  font-size: 18px;
  font-family: $font-regular;
  color: $gray-text;
  margin: 0 0 0 0;
  line-height: 18px;
  margin-bottom: 15px;

  .original-price {
    text-decoration: line-through;
  }

  .discount-price {
    color: $pink;
  }
}

.cart-button {
  button {
    background: $blue;
    border: none;
    transition: ease 0.5s;
    font-size: 16px;
    line-height: 24px;
    padding: 9px 31px;
    color: $white;
    border-radius: 6px;
    text-transform: capitalize;

    &:hover {
      background: $blue-dark;
    }
  }
}

.family-type {
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  width: 99%;
  display: inline-block;
  line-height: 20px;
}

.col-3 {
  .fontcard {
    width: calc(33.3% - 20px);
  }

  .actionbar {
    display: block;
    text-align: center;
    padding: 15px;
  }
}

@media (max-width:600px) {
  .col-3, .col-2 {
    .fontcard {
      width: calc(100% - 20px);
    }

  .actionbar {
    display: block;
    text-align: center;
    padding: 15px;
    }
  }
} 