@import url(//db.onlinewebfonts.com/c/0ec2c5a68270757c2200d436b6904b60?family=MFRooneySans-Regular);


.heading-large {
    text-align: center;
    position: relative;
    margin: 20px 0 54px;

    &:after{
      position: absolute;
      content: '';
      top: 60px;
      height: 1px;
      background: $gray-border;
      width: 87%;
      left: 50%;
      transform: translateX(-50%);
    }
}